.answer {
    height: 50px;
    display: flex;
    justify-content: space-around;
    background: rgba(0,0,0,.1);
    border: 4px double #ff0000;
    color: aquamarine;
    font-size: 30px;
    align-content: center;
    padding: 10px;
}
.wrong {
    background: #791e1e;
}
.correct {
    background: #166e30;
}
.a_list {
    display: flex;
    flex-direction: column;
}
